import React from 'react';
import { isValidPhoneNumber } from 'react-phone-number-input';
import ReactPhoneInput from 'react-phone-input-2';
// import 'react-phone-input-2/lib/style.css';
import 'react-phone-input-2/lib/material.css';
import { getCountryCallingCode, parsePhoneNumberFromString } from 'react-phone-number-input/libphonenumber';

class MobileNumber extends React.Component {

  state = {
    countryCode: this.props.countryCode ? this.props.countryCode : 'us',
    phone: this.props.phone ? this.props.phone : ''
  }

  componentWillReceiveProps = (nextProps) => {
    if (nextProps.countryCode && nextProps.countryCode.toLowerCase() !== this.state.countryCode) {
      this.setCountryCode(nextProps.countryCode.toLowerCase(), nextProps.phone);
    } else {
      if (nextProps.phone !== this.state.phone) {
        this.setState({ phone: nextProps.phone })
      }
      const parsePhoneNumber = parsePhoneNumberFromString(`phone: ${nextProps.phone}`);
      if (parsePhoneNumber && parsePhoneNumber.country && parsePhoneNumber.country.toLowerCase() !== this.state.countryCode) {
        this.setState({ countryCode: parsePhoneNumber.country.toLowerCase() }, () => {
          this.setState({ phone: nextProps.phone });
        })
      }
    }
  }

  setCountryCode = (countryCode, phone) => {
    this.setState({ countryCode, phone });
  }

  render() {
    return (
      <>
        <ReactPhoneInput
          defaultCountry={this.state.countryCode}
          country={this.state.countryCode}
          enableSearchField={true}
          disableAreaCodes={true}
          countryCodeEditable={false}
          value={this.state.phone}
          onChange={(phone, data) => {
            if (phone) {
              if (data.countryCode.toLowerCase() !== this.state.countryCode) {
                this.setCountryCode(data.countryCode.toLowerCase(), phone);
              } else { }
              let phoneNumber = '+' + phone.replace(/[^0-9]+/g, '');
              let countryCode = getCountryCallingCode(data.countryCode.toUpperCase());
              this.props.updateMobileNumber(phoneNumber, isValidPhoneNumber(phoneNumber), data.countryCode.toLowerCase());
              // this.props.updateMobileNumber(phoneNumber, isValidPhoneNumber(phoneNumber) || phoneNumber.replace('+' + countryCode, '').length === 0);
            }
          }}
        />
      </>
    )
  }

}

export default MobileNumber;
