import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import './Topbar.css'
import { connect } from 'react-redux';
import logo from '../../../assets/logo.png';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { AppBar, Toolbar, Typography, IconButton, Tooltip } from '@material-ui/core';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import ArrowBackIcon from '@material-ui/icons/ArrowBackRounded';

import { signOut } from '../../../modules/Authentication/AuthenticationActions';
import MessageModal from "../../../utils/components/MessageModal/MessageModal";

const styles = () => ({
  root: {
    boxShadow: 'none'
  },
  rootPaddingTop: {},
  imageLogo: {
    width: '100px',
    height: '50px'
  },
  title: {
    color: '#fff'
  }
});

class Topbar extends React.Component {

  constructor(props) {
    super(props);

    this.menu = this.props.menu;
    this.state = {}
  }

  logoutUser = () => {
    this.refs.messageModal.setState({
      showModal: true, message: 'Are you sure you want to logout?', title: 'Log out',
      showOkButton: true,
      showCancelButton: true,
      handleOk: this.props.signOut,
      handleCancel: () => {
      },
      okText: 'Logout',
      cancelText: 'Cancel'
    });
  };

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.rootPaddingTop}>
        <AppBar
          className={classes.root}
          color="primary"
          position="fixed"
        >
          <Toolbar>
            <RouterLink to="/">
              {
                !this.props.pageTitle
                  ? (
                    <img
                      alt="Logo"
                      src={logo}
                      className={classes.imageLogo}
                    />
                  )
                  : (
                    <Typography
                      align="center"
                      className={classes.title}
                      variant="h4"
                    >
                      {/* {
                      this.props.activeStep > 0
                      ?(
                        <ArrowBackIcon onClick={() => this.props.prevButtonClick()} />
                      )
                      :<></>
                    } */}
                      {this.props.pageTitle}
                    </Typography>
                  )
              }
            </RouterLink>
            <Tooltip title="Logout" placement="top">
              <IconButton className="iconButtonOnRight" onClick={() => this.logoutUser()}>
                <ExitToAppIcon className="exitToAppIcon" />
              </IconButton>
            </Tooltip>

          </Toolbar>
        </AppBar>

        <MessageModal
          ref="messageModal" />
      </div>
    );
  }

};

Topbar.propTypes = {
  className: PropTypes.string
};

const mapStateToProps = state => {
  return {
    loggedInUser: state.auth.loggedInUser,
    loggedInUserPractice: state.auth.loggedInUserPractice,
    currentRole: state.auth.currentRole
  }
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    signOut: () => {
      dispatch(signOut())
    }
  }
};

Topbar = connect(
  mapStateToProps,
  mapDispatchToProps
)(Topbar);

export default withStyles(styles)(Topbar);
